import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { RiHome2Line } from "react-icons/ri";
import { GiBrain } from "react-icons/gi";
import { SiPolymerproject } from "react-icons/si";
import { BsEnvelope } from "react-icons/bs";
import { FaTimes, FaBars } from "react-icons/fa";
import { AiOutlineCloseCircle } from "react-icons/ai";
import { makeStyles } from "@material-ui/core/styles";
import ToggleBtn from "../toggle-btn/ToggleBtn";
import DialogTitle from "@material-ui/core/DialogTitle";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import SocialData from "../../Data/socials-data";

import "./navbar.css";
import "../neon.css";
const useStyles = makeStyles({
    rootContentDialog: {
        padding: 0,
    },
    rootDialog: {
        background: "linear-gradient(-45deg, #000000,  #434343)",
    },
    rootFa: {
        fontFamily: "Amiri, serif",
        direction: "rtl",
    },
    dialogTitle: {
        fontSize: "1.5em",
        // borderBottom: "1.5px solid  rgba(68, 63, 63, 0.141)",
        boxShadow: "0px 5px 25px -14px black",
        fontFamily: "Vibur",
        textTransform: "capitalize",
    },
});
function Navbar(props) {
    const { langToggle, setLangToggle } = props;
    const classes = useStyles();
    const [openD, setOpenD] = useState(false);
    const [burger, setburger] = useState(false);
    const [button, setButton] = useState(true);
    const [hoveredIcon, setHoveredIcon] = useState("");
    const [activeIcon, setActiveIcon] = useState("");
    document.title = "Faez Tgh | " + activeIcon;
    const handleClose = () => {
        setOpenD(false);
    };

    const handleClickOpen = () => {
        setOpenD(true);
    };

    const handleburger = () => setburger(!burger);
    const closeMobileMenu = () => setburger(false);

    const showButton = () => {
        if (window.innerWidth <= 630) {
            setButton(false);
            setburger(false);
        } else {
            setButton(true);
        }
    };

    useEffect(() => {
        window.addEventListener("resize", showButton);

        return () => {
            window.removeEventListener("resize", showButton);
        };
    }, []);

    return (
        <>
            <div
                className={
                    burger ? "navbar-container nav-active" : "navbar-container"
                }
            >
                <div className="nav-logo">
                    <Link to="/" onClick={closeMobileMenu}>
                        <div className="neon">
                            <p>
                                <span>F</span>
                                <span>a</span>
                                <span>e</span>
                                <span>z</span>
                            </p>
                        </div>
                    </Link>
                </div>

                <div className="menu-icon" onClick={handleburger}>
                    {!burger ? <FaBars /> : <FaTimes />}
                </div>

                <ul className={burger ? "nav-links ul-active" : "nav-links"}>
                    {NavData.map((item) => {
                        return (
                            <li
                                key={item.id}
                                onMouseOver={() => setHoveredIcon(item.name)}
                                onMouseOut={() => setHoveredIcon("")}
                                onClick={() => setActiveIcon(item.name)}
                            >
                                <Link
                                    className="nav-link "
                                    to={item.link}
                                    onClick={closeMobileMenu}
                                >
                                    <div
                                        className={
                                            hoveredIcon === item.name
                                                ? "hide-item"
                                                : "unhide-item"
                                        }
                                    >
                                        <div
                                            className={
                                                activeIcon === item.name &&
                                                "active-icon"
                                            }
                                        >
                                            {item.icon}
                                        </div>
                                    </div>
                                    <p
                                        className={
                                            hoveredIcon !== item.name
                                                ? "hide-item"
                                                : "logo-name"
                                        }
                                    >
                                        <div
                                            className={
                                                activeIcon === item.name &&
                                                "active-icon"
                                            }
                                        >
                                            {langToggle
                                                ? item.name
                                                : item.nameFa}
                                        </div>
                                    </p>
                                </Link>
                            </li>
                        );
                    })}

                    <li>
                        <div className="toggleBtn">
                            <ToggleBtn
                                id="lang-toggle"
                                onClick={() => setLangToggle(!langToggle)}
                                on="EN"
                                off="FA"
                            />
                        </div>
                    </li>

                    <li>
                        <div className="follow" onClick={handleClickOpen}>
                            {langToggle ? (
                                <h1>Follow +</h1>
                            ) : (
                                <h1 className={classes.rootFa}>دنبال‌کن‌+</h1>
                            )}
                        </div>
                    </li>
                </ul>

                <Dialog
                    onClose={handleClose}
                    // aria-labelledby="simple-dialog-title"
                    open={openD}
                >
                    <DialogTitle
                        id="simple-dialog-title"
                        className={classes.dialogTitle}
                    >
                        {langToggle ? (
                            <h4>+ Follow Me in Social Media </h4>
                        ) : (
                            <h4 className={classes.rootFa}>
                                + من رو در شبکه های اجتماعی دنبال کنید
                            </h4>
                        )}
                    </DialogTitle>
                    <DialogContent className={classes.rootContentDialog}>
                        <DialogContentText>
                            {SocialData.map((item) => {
                                return (
                                    <ul
                                        key={item.id}
                                        className="soacial-icons-wrapper"
                                    >
                                        <li className="social-icons">
                                            <a
                                                href={item.link}
                                                target="_blank"
                                                rel="noreferrer"
                                            >
                                                {item.icon}
                                                <h5>{item.name}</h5>
                                            </a>
                                        </li>
                                    </ul>
                                );
                            })}
                        </DialogContentText>
                    </DialogContent>
                    <DialogActions>
                        <button
                            onClick={handleClose}
                            className="social-dialog-close-btn"
                        >
                            <AiOutlineCloseCircle size={30} />
                        </button>
                    </DialogActions>
                </Dialog>
            </div>
        </>
    );
}

export default Navbar;

const NavData = [
    {
        id: 0,
        name: "Home",
        nameFa: "خانه",
        link: "/",
        icon: <RiHome2Line />,
    },
    {
        id: 1,
        name: "Projects",
        nameFa: "پروژه‌ها",
        link: "/projects",
        icon: <SiPolymerproject />,
    },
    {
        id: 2,
        name: "Skills",
        link: "/skills",
        nameFa: "مهارت‌ها",

        icon: <GiBrain />,
    },
    {
        id: 3,
        name: "Contact",
        nameFa: "تماس",
        link: "/contact",
        icon: <BsEnvelope />,
    },
];
