import React from "react";
import "./home.css";
import Grid from "@material-ui/core/Grid";
import "react-awesome-slider/dist/styles.css";
import Button from "@material-ui/core/Button";
import { BsEnvelope } from "react-icons/bs";
import { Link } from "react-router-dom";
import { SiPolymerproject } from "react-icons/si";
import { EN, FA } from "../../../Data/lang-data";
import PresentCard from "../../PresentCard";
function Home(props) {
    const { langToggle } = props;
    return (
        <>
            <Grid className="home-grid-container" container spacing={1}>
                <Grid className="left-wrapper" item xs={12} md={6} lg={6}>
                    {langToggle ? EN.homeIntro : FA.homeIntro}

                    {langToggle ? EN.homeOutro : FA.homeOutro}

                    <div className="home-buttons-container" dir="ltr">
                        <Button
                            className="home-contact-btn"
                            variant="outlined"
                            color="secondary"
                            startIcon={<BsEnvelope />}
                            size="large"
                        >
                            <Link to="/contact">
                                {langToggle ? EN.contactBtn : FA.contactBtn}
                            </Link>
                        </Button>
                        <Button
                            className="home-contact-btn"
                            variant="contained"
                            color="default"
                            startIcon={<SiPolymerproject />}
                            size="large"
                        >
                            <Link to="/projects">
                                {langToggle ? EN.projectsBtn : FA.projectsBtn}
                            </Link>
                        </Button>
                    </div>
                </Grid>
                <Grid className="right-wrapper" item xs={12} md={6} lg={6}>
                    <div className="circles">
                        <img
                            className="circle-img"
                            src={process.env.PUBLIC_URL + "/assets/eclips.png"}
                            alt=""
                        />
                        <img
                            className="small-circle-img"
                            src={process.env.PUBLIC_URL + "/assets/eclips.png"}
                            alt=""
                        />
                      
                    </div>
                </Grid>
            </Grid>
        </>
    );
}

export default Home;
