import React, { useState } from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Home from "./components/Pages/Home/Home";
import Projects from "./components/Pages/Projects/Projects";
import Navbar from "./components/Navbar/Navbar";
import Contact from "./components/Pages/Contact/Contact";
import Skills from "./components/Pages/Skills/Skills";
import Preloader from "./components/preloader";
import { makeStyles } from "@material-ui/core/styles";
import "./App.css";

const useStyle = makeStyles((langToggle) => ({
    root: {
        animation: "$fadeAnim  1.5s linear",
    },

    rootFa: {
        // fontFamily: "Amiri, serif",
        animation: "$fadeAnim  1.5s linear",
    },

    "@keyframes fadeAnim": {
        from: { opacity: 0 },
        to: { opacity: 1 },
    },
}));

function App() {
    const [langToggle, setLangToggle] = useState(true);
    const [loader, setLoader] = useState(true);
    const classes = useStyle(langToggle);

    if (!langToggle) {
        document.body.dir = "rtl";
    } else {
        document.body.dir = "ltr";
    }

    setTimeout(() => {
        setLoader(false);
    }, 3000);

    return (
        <>
            <Router>
                {loader ? (
                    <Preloader open={loader} />
                ) : (
                    <div className={langToggle ? classes.root : classes.rootFa}>
                        <div className="app-container">
                            <div className="app-navbar">
                                <Navbar
                                    langToggle={langToggle}
                                    setLangToggle={setLangToggle}
                                />
                            </div>
                            <div className="main-container">
                                <Switch>
                                    <Route
                                        exact
                                        path="/"
                                        render={(props) => (
                                            <Home langToggle={langToggle} />
                                        )}
                                    />
                                    <Route
                                        path="/projects"
                                        render={(props) => (
                                            <Projects langToggle={langToggle} />
                                        )}
                                    />
                                    <Route
                                        path="/contact"
                                        render={(props) => (
                                            <Contact langToggle={langToggle} />
                                        )}
                                    />
                                    <Route
                                        path="/skills"
                                        render={(props) => (
                                            <Skills langToggle={langToggle} />
                                        )}
                                    />
                                </Switch>
                            </div>
                        </div>
                    </div>
                )}
            </Router>
        </>
    );
}

export default App;
