import React, { useState } from "react";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import { useTheme, makeStyles } from "@material-ui/core/styles";
import { AiOutlineCloseCircle } from "react-icons/ai";

const useStyles = makeStyles(() => ({
    dialogActionBtn: {
        fontSize: "0.7em",
        color: "#f32552",
    },
    dialogTitle: {
        fontSize: "1.5em",
        borderBottom: "1px solid gray",
        fontFamily: "Vibur",
        textTransform: "capitalize",
        padding: 0,
    },
    rootFa: {
        fontFamily: "Amiri, serif ",
        letterSpacing: 1,
        padding: "0px 10px !important",
        
    },
}));
function MyDialog(props) {
    const classes = useStyles();
    const { open, size, setOpen, item, langToggle } = props;
    const theme = useTheme();
    const fullScreen = useMediaQuery(theme.breakpoints.down(size));

    const handleClose = () => {
        setOpen(false);
    };

    return (
        <>
            <div>
                <Dialog
                    fullScreen={fullScreen}
                    open={open}
                    onClose={handleClose}
                    aria-labelledby="responsive-dialog-title"
                >
                    <DialogTitle id="responsive-dialog-title">
                        <div className={classes.dialogTitle}>
                            {langToggle ? item.EN.name : item.FA.name}
                        </div>
                    </DialogTitle>
                    <DialogContent>
                        <DialogContentText
                            className={!langToggle && classes.rootFa}
                        >
                            {langToggle ? item.EN.desc : item.FA.desc}
                        </DialogContentText>
                    </DialogContent>
                    <DialogActions>
                        <button
                            className="go-project-close-btn"
                            onClick={handleClose}
                        >
                            <AiOutlineCloseCircle size={30} />
                        </button>
                        <a
                            href={item.EN.link}
                            target="_blank"
                            rel="noreferrer"
                            className="go-project-btn"
                        >
                            {langToggle ? (
                                <h1> Go to Project</h1>
                            ) : (
                                <h1 className={classes.rootFa}>نمایش پروژه</h1>
                            )}
                        </a>
                    </DialogActions>
                </Dialog>
            </div>
        </>
    );
}

export default MyDialog;
