import React from "react";
import "./skill-card.css";
import { EN, FA } from "../../Data/lang-data";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles(() => ({
    rootFa: {
        fontFamily: "Amiri, serif ",
    },
}));
function SkillCard(props) {
    const { langToggle, skillDesc, skillIcon, title, skillLibs } = props;
    const classes = useStyles();

    return (
        <>
            <div
                className="skill-card"
                dir={langToggle ? EN.direction : FA.direction}
            >
                <div className="card-wrapper">
                    <div className="skill-icon">{skillIcon}</div>
                    <div className="skill-title">
                        <h1>{title}</h1>
                    </div>
                    <div
                        className="skill-desc"
                        dir={langToggle ? EN.direction : FA.direction}
                    >
                        <p className={!langToggle && classes.rootFa}>
                            {skillDesc}
                        </p>
                    </div>
                    <div className="skill-libs">
                        {skillLibs.map((item) => {
                            return (
                                <div key={item} className="skill-lib">
                                    {item}
                                </div>
                            );
                        })}
                    </div>
                </div>
            </div>
        </>
    );
}

export default SkillCard;
