import React, { useEffect, useState } from "react";
import { GrReactjs } from "react-icons/gr";
import { SiW3C } from "react-icons/si";
import { FcAndroidOs } from "react-icons/fc";
import { FaJava } from "react-icons/fa";
import { FaGitAlt } from "react-icons/fa";
import { CgFigma } from "react-icons/cg";
import { FcLinux } from "react-icons/fc";

import Grid from "@material-ui/core/Grid";
import SkillsData from "../../../Data/skills-data";
import { makeStyles } from "@material-ui/core/styles";
import { EN, FA } from "../../../Data/lang-data";

import "./skills.css";
import SkillCard from "../../Skill-Card/SkillCard";

const useStyle = makeStyles(() => ({
    root: {
        animation: "$fadeAnim  1s linear",
    },

    "@keyframes fadeAnim": {
        from: {
            opacity: 0,
        },
        to: {
            opacity: 1,
        },
    },
}));
function Skills(props) {
    const classes = useStyle();

    const [clickedSkill, setclickedSkill] = useState("React");

    const data = SkillsData.map((obj) => obj);
    const onSkillClick = () => {
        let skillObj = data.map((item) => {
            return clickedSkill === item.EN.skillName && item;
        });

        skillObj = skillObj.filter((obj) => {
            return obj;
        });
        return skillObj;
    };

    const [currentSkillObj, setCurrentSkillObj] = useState(onSkillClick);
    const handleCurrentSkill = () => {
        setCurrentSkillObj(onSkillClick);
    };

    useEffect(() => {
        handleCurrentSkill();
    }, [clickedSkill]);

    return (
        <>
            <div className="skills-container">
                <Grid container spacing={0} className={classes.root}>
                    <Grid
                        item
                        xs={12}
                        sm={12}
                        lg={6}
                        md={6}
                        className="skills-col-wrapper"
                    >
                        <GrReactjs
                            color="#00a2ff"
                            onClick={() => setclickedSkill("React")}
                        />
                        <FcAndroidOs
                            onClick={() => setclickedSkill("Android")}
                        />
                        <FaJava
                            color="#ff0000"
                            onClick={() => setclickedSkill("Java")}
                        />
                        <SiW3C
                            color="#fff"
                            onClick={() => setclickedSkill("HTML - CSS - JS")}
                        />
                        <FaGitAlt
                            color="#ff0000"
                            onClick={() => setclickedSkill("Git")}
                        />
                        <CgFigma
                            color="#e91aed"
                            onClick={() => setclickedSkill("Figma")}
                        />
                        <FcLinux onClick={() => setclickedSkill("Linux")} />
                    </Grid>
                    <Grid
                        item
                        xs={12}
                        sm={12}
                        lg={6}
                        md={6}
                        className="skill-present-wrapper"
                    >
                        <SkillCard
                            skillIcon={currentSkillObj[0].EN.skillIcon}
                            title={currentSkillObj[0].EN.skillName}
                            skillDesc={
                                props.langToggle
                                    ? currentSkillObj[0].EN.skillDesc
                                    : currentSkillObj[0].FA.skillDesc
                            }
                            skillLibs={currentSkillObj[0].EN.skillLibs}
                            langToggle={props.langToggle}
                        />
                    </Grid>
                </Grid>
            </div>
        </>
    );
}

export default Skills;
